import { Game, GameEvent, LivingEntity, Option } from "../../interfaces/interfaces";
import { test } from "../../utils/math";
import { EventData } from "../GameEvent";

interface CombatEventData extends EventData {
    enemy: LivingEntity;
}

class CombatEvent extends GameEvent {
    enemy: LivingEntity;

    constructor(data: CombatEventData) {
        super({
            ...data,
            priority: 10,
        });
        this.enemy = data.enemy;
    }

    onStart(game: Game) {
        game.appendText(`Âú®‰Ω†Èù¢ÂâçÁöÑÊòØ‰∏Ä‰∏™Â¶ÇÊöóÂ§úËà¨ÊºÜÈªëÁöÑÊÄ™Áâ©‚Äî‚Äî${this.enemy.name}(${this.enemy.health}/${this.enemy.maxHealth})`);
    }

    onRender(game: Game): Array<Option> {
        const player = game.getPlayer();
        const previewDamage = player.getWeapon().previewDamage(game, this.enemy);
        return [
            { 
                text: `ÊîªÂáª`,
                leftText: 'üó°',
                rightText: `${previewDamage}‚ô•`,
                tag: 'attack',
            },
            { 
                text: `ÈÄÉË∑ë`,
                leftText: 'üèÉ‚Äç',
                rightText: `${player.dexterity}%`,
                tag: 'escape',
            },
        ].concat(game.debugMode ? [{
            text: `‰∏ÄÂáªÂøÖÊùÄ`,
            leftText: 'üíª',
            rightText: `Ë∞ÉËØïÊ®°Âºè`,
            tag: 'one_punch',
        }] : []);
    }

    onInput(game: Game, option: Option) {
        let escaped: boolean = false;
        const p = game.getPlayer();
        const e = this.enemy;

        if (option.tag === 'one_punch') {
            e.mutateValue(game, 'health', -e.health, 'Âõ†‰∏∫ËãüÁÆ°ÁêÜ');
        } else if (option.tag === 'attack') {
            if (test(this.enemy.dexterity)) {
                game.appendText(this.enemy.name + 'Ë∫≤ÂºÄ‰∫Ü‰Ω†ÁöÑÊîªÂáª');
                if (test(this.enemy.dexterity)) {
                    game.appendText('Âπ∂Áªô‰∫Ü‰Ω†‰∏ÄÊã≥');
                    e.getWeapon().onAttack(game, p);
                }
            } else {
                const weapon = p.getWeapon();
                weapon.onAttack(game, e);
            }
        } else if (option.tag === 'escape') {
            if(test(p.dexterity) || !test(e.dexterity)) {
                escaped = true;
                game.appendText('‰Ω†ÈÄÉ‰∫ÜÂá∫Âéª');
                game.endEvent(this);
            } else {
                game.appendText('‰Ω†Ê≤°ÊúâÈÄÉË∑ëÊàêÂäü');
                if (test(this.enemy.strength)) {
                    game.appendText('ËÄå‰∏îËøòË¢´Áà™Â≠êÊäì‰º§');
                    e.getWeapon().onAttack(game, p);
                }
            }
        }

        if (this.enemy.health <= 0) {
            game.appendText('‰Ω†ÊâìË¥•‰∫Ü' + this.enemy.name);
            game.endEvent(this);
            // game.triggerEvent('end');
        } else if (escaped) {
            game.appendText(`‰Ω†ÊàêÂäüÈÄÉÁ¶ª‰∫Ü${this.enemy.name}ÁöÑËøΩÊùÄ`);
        } else {
            game.appendText(`${this.enemy.name}‰æùÁÑ∂Â≠òÊ¥ª(${this.enemy.health}/${this.enemy.maxHealth})`);
            game.appendText(`Âà∞‰∫Ü${this.enemy.name}ÁöÑÂõûÂêà`);
            if (test(game.getPlayer().dexterity)) {
                game.appendText('‰Ω†ÈÄÉËøá‰∫ÜÂÆÉÁöÑÂÅ∑Ë¢≠');
            } else {
                game.appendText('‰Ω†Ë¢´Áà™Â≠êÊäì‰º§');
                e.getWeapon().onAttack(game, p);
            }
        }
    }
}

export default CombatEvent;
export type { CombatEventData };
